import React, { Component } from 'react';
import { Player } from 'video-react';
import './App.css';

class App extends Component {
  render() {
    return (
    <Player width={500} fluid={false}>
      <source src="./m/2018-09-08-17-42-05.mp4" />
    </Player>
    );
  }
}

export default App;



